html {
    scroll-behavior: smooth
}

body {
    font-family: 'Alegreya Sans SC'
}

section {
    text-align: justify
}

.col-6 {
    width: 100%;
    display: inline-table
}

footer {
    background-color: #171717;

    padding-top: 2rem;
    font-family: 'Alegreya Sans SC';
    border-top: 2px solid red;
    position: fixed;
    width: 100%;
    bottom: 0;
    height: 10rem;
}

footer .logo {
    color: #fff;
    font-size: 28px
}

footer .row {
    display: flex;
    color: #fff;
margin-top:4rem;
 
    justify-content: space-evenly;
 
}
footer .row2 {
    display: flex;
    color: #fff;

    
    
    justify-content: space-evenly;

}



footer ul {
    padding: 0;
    transition: all .3s ease-in-out
}

footer ul li {
    list-style-type: none;
    padding: 0;
    transition: all .5s ease-in-out;
    line-height: 2
}

.col-3 {
    display: inline-table;
    width: 25%
}

@media only screen and (max-width :1024px) {
    .col-3 {
        display: block;
        width: 100%
    }

    footer {
        position: fixed;
        width: 100%;
        bottom: 0
    }


    footer .row {
        display: flex;
        color: #fff;
        width: 100%;
        justify-content: space-between
    }
}

@media only screen and (min-width :768px) and (max-width :1024px) and (orientation :portrait) {
    footer {
        padding-right: 3em;
        padding-left: 3em
    }

    .col-3 {
        display: block;
        width: 100%;
        padding-bottom: 1em
    }
}

@media only screen and (min-width :768px) and (max-width :1024px) and (orientation :landscape) {
    section {
        padding-bottom: 3em
    }

    .col-3 {
        display: block;
        width: 100%;
        padding-bottom: 1em
    }

    footer .row {
        display: flex;
        color: #fff;
        width: 85%;
        justify-content: space-between
    }
}

@media only screen and (max-width :600px) {
    .col-3 {
        display: block;
        width: 100%;
        padding-bottom: 1em
    }

    .col-6 {
        width: 100%;
        display: block
    }

    footer {
        position: fixed
    }
}

@media only screen and (max-width :480px) {
    .col-6 {
        width: 100%;
        display: block
    }

    footer {
        padding-right: 1em;
        padding-left: 1em;
        position: fixed;
        width: 100%;
        height: 5.5rem;
   
    }

    footer .row {
        display: flex;
        width: 95%;
        padding-top: 1.3rem;
   margin-top: 0rem;
        justify-content: space-between;
        align-items: center;
  
    }

    footer .row2 {
        display: flex;
        margin-top: -.8rem;
        padding-bottom: .9rem;
  color: white;
        width: 90%;
   
    
        align-items: center;
        flex-wrap: wrap;
     
    }
}
